import { html } from '../config';

export const Box = (dir: 'row' | 'column', gap: number, ...children: HTMLElement[]) =>
  html.div(
    ['style', 'flexDirection', dir],
    ['style', 'alignItems', 'center'],
    ['style', 'boxSizing', 'border-box'],
    ['style', 'display', 'flex'],
    ['style', 'fontFamily', 'Space Grotesk'],
    ['style', 'gap', `${gap}px`],
  )(...children);
